/* General body styles */
body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f9;
  color: #333;
  margin: 0;
  padding: 20px;
}

/* Styling for the main container */
div {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 15px rgba(0,0,0,0.1);
}

/* Styles for labels */
label {
  font-weight: bold;
  display: block;
  margin: 20px 0 10px;
}

/* Styles for inputs */
input[type='text'], input[type='number'] {
  width: calc(100% - 22px);
  padding: 10px;
  margin-bottom: 20px;
  border: 2px solid #ccc;
  border-radius: 4px;
  display: block;
}

input[type='text']:focus, input[type='number']:focus {
  border-color: #007bff;
}

/* Styling for buttons */
button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  margin-right: 10px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #0056b3;
}

button:active {
  background-color: #004494;
}

/* Special button styles */
button#reset {
  background-color: #dc3545;
}

button#reset:hover {
  background-color: #c82333;
}


p {
  line-height: 1.6;
  margin: 10px 0;
}

/* Styling for the Toggle component and its button */
.Toggle-button {
  background-color: #28a745;
  color: #ffffff;
}

.Toggle-button:hover {
  background-color: #218838;
}

/* Footer styles */
footer {
  text-align: center;
  margin-top: 20px;
  font-size: 0.8em;
  color: #666;
}
